import React, { Component, Suspense } from 'react';
import InfiniteScroll from "react-infinite-scroll-component";
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import Media from '../../CommonComponents/Media/Media';
import EmptyTrash from './EmptyTrash';

class InfiniteScrollMasonry extends Component {


    render() {
        const { imagesData, showModal, modalAssetData, fetchData, refresh, handleModalShow, onClickPutBack, onClickDeletePermanently, onClickToSelect } = this.props;

        return imagesData.length > 0 ? <>
            <InfiniteScroll
                dataLength={imagesData.length}
                next={fetchData}
                hasMore={true}
                // scrollableTarget={document.getElementsByClassName("main_content")}
                // loader={<div className="loader_default">Loading...</div>}
                scrollThreshold="100px"
                endMessage={
                    <p>

                    </p>
                }
                refreshFunction={refresh}
                pullDownToRefresh
                pullDownToRefreshThreshold={50}
                pullDownToRefreshContent={
                    <h3>Pull down to refresh</h3>
                }
                releaseToRefreshContent={
                    <h3>Release to refresh</h3>
                }>


                <div className={"row gx-2"}>
                    {imagesData && imagesData.map((photo, index) => {
                        return <div key={`scroll_${photo.id}`} className="col-xxl-1 col-xl-2 col-md-3 col-sm-3 col-6  custom-col-p">
                            <ContextMenuTrigger id={`cm_trash_dropDown-${photo.id}`}>
                                <div className={`card card-custom mb-2 rounded-1 ${photo.isSelect ? ' active' : ''}`}>
                                    <div className="card-body d-flex flex-column p-1">
                                        <div className="d-flex align-items-center justify-content-between pb-1">
                                            <div className="d-flex align-items-center">
                                                <label className="form-check me-2">
                                                    <input className="form-check-input" type="checkbox" name="" value="phone" checked={photo.isSelect ? true : false} onClick={onClickToSelect(photo.referenceId)} />
                                                </label>
                                            </div>
                                        </div>
                                        <div className="d-flex align-items-center cursor-pointer" animation={'fade'} onClick={handleModalShow(photo,index)}>
                                            <Media mediaType={photo.type} postFile={photo.postFile} mediaHeight={photo.mediaHeight} mediaWidth={photo.mediaWidth} socialNetwork={photo.networkId} textContent={photo.content} rating={photo.rating} alt={photo.name} />
                                        </div>
                                    </div>
                                </div>
                            </ContextMenuTrigger>
                            <ContextMenu className="dropdown-menu show context_menu" id={`cm_trash_dropDown-${photo.id}`} hideOnLeave={true}>
                                <div className="menu-item px-2">
                                    <div className="menu-content text-muted px-2 py-1 fs-8 text-uppercase">Choose your actions</div>
                                </div>
                                <div className="menu-item" onClick={onClickPutBack(photo)}>
                                    <MenuItem className="dropdown-item">
                                        <i className="ri-restart-line icon-6 me-1"></i>
                                        <span className="menu-title">Restore</span>
                                    </MenuItem>
                                </div>
                                <div className="menu-item" onClick={onClickDeletePermanently(photo)}>
                                    <MenuItem className="dropdown-item">
                                        <i className="ri-delete-bin-6-line icon-6 me-1"></i>
                                        <span className="menu-title">Delete Permanently</span>
                                    </MenuItem>
                                </div>

                                <MenuItem divider className="my-1" />
                                <div className="menu-item" onClick={handleModalShow(photo)}>
                                    <MenuItem className="dropdown-item">
                                        <i className="ri-information-line icon-6 me-1"></i>
                                        <span className="menu-title">View Details</span>
                                    </MenuItem>
                                </div>

                            </ContextMenu>
                        </div>

                    })}
                </div>
            </InfiniteScroll>
            {/* <EmptyTrash /> */}
        </> : null

    }
}

export default InfiniteScrollMasonry;